
/* main header box */
.header{
    height: 100vh;
    font-family: 'Lato', sans-serif;
    background: linear-gradient(to right bottom, #7ed56f, #28b485);
    /* clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 90% 100%, 0% 100%); */
}

.box{
    display: grid;
    height: 100%;
    /* flex-direction: row; */
    grid-template-columns: 1.5fr 1fr;
}

.left-box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    /* grid-template-rows: 1fr 1fr 2fr; */
}

.logo-box{
    width: 30%;
    height: 30px;
    position: absolute;
    left: 20px;
    top: 5px;
}

.logo-box img{
    width: 50%;
    height: 10rem;
}

.left-box h1{
    font-size: 3rem;
    text-transform: uppercase;
    font-weight: 900;
    letter-spacing: 1.4rem;
    /* animation-name: moveInLeft;
    animation-duration: 1s; */
    animation: moveInLeft .9s ease-out;
}

.left-box h3{
    font-size: 1.2rem;
    font-weight: 400;
    letter-spacing: 0.6rem;
    /* animation-name: moveInRight; */
    /* animation-duration: 1s; */
    animation: moveInRight .9s ease-out;
}

.right-box{
    height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    position: relative;
}

.img-main{
    width: 100%;
    height: 90%;
    position: absolute;
    bottom: 0;
    right: 20;
    align-self: start;
}

/* animation */

/* moveINLeft */
@keyframes moveInLeft {
    0%{
        opacity: 0;
        transform: translateX(-100px);
    }
    80%{

    }
    100%{
        opacity: 100;
        transform: translateX(0);
    }
}

/* moveInRight */
@keyframes moveInRight {
    0%{
        opacity: 0;
        transform: translateX(100px);
    }

    80%{

    }

    100%{
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes moveInBottom {
    0%{
        opacity: 0;
        transform: translateY(60px);
    }

    100%{
        opacity: 100;
        transform: translateY(0);
    }
}


/* animated button */

.btn-div{
    /* margin-top: 40px; */
    position:absolute;
    bottom: 20%;
}


.btn:link,
.btn:visited{
    text-transform: uppercase;
    text-decoration: none;
    padding: 15px 40px;
    display: inline-block;
    border-radius: 100px;
    transition: all .2s;
    position: relative;
}

.btn-white{
    background-color: #fff;
    color: #777;
    align-self: center;
}

.btn:hover{
    transform: translateY(-3px);
    box-shadow: 0 10px 20px rgba(0,0,0,.2);
}

.btn:active{
    transform: translateY(-1px);
    box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
}

/* psuedo .btn after */

.btn::after{
    content: "";
    display: inline-block;
    height: 100%;
    width: 100%;
    border-radius: 100px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    transition: all 0.4s;
}

.btn-white::after{
    background-color: #fff;
}

.btn:hover::after{
    transform: scaleX(1.5) scaleY(1.6);
    opacity: 0;
}

.btn-animated{
    animation: moveInBottom 0.4s ease-out 0.75s;
    animation-fill-mode: backwards;
}

/* Responsivesness */
/*==========Media Query (Medium Devices "Tab")==========*/

@media screen and (max-width:1024px) {
    header{
        height: 100vh;
    }
}

