@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&family=Poppins:wght@300;400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;900&family=Poppins:wght@300;400;500;600&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
    outline: 0;
}

:root {
    /* --color-bg: #28b485; */
    --color-bg: #f0f0f0;
    --color-bg-variant: #7ed56f;
    --color-primary: #55c57a;
    --color-primary-variant: #eaf9e7;
    --color-white: #fff;
    --color-light: #fdf7f0;
    --transition: all 400ms ease;
    --container-width-lg: 100%;
    --container-width-md: 86%;
    --container-width-sm: 100%;
   /*
COLORS:
Light green: #7ed56f
Medium green: #55c57a
Dark green: #28b485
*/

}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Lato','Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    position: relative;
    /* padding: 20px; */
    /* background-image: url('../src/assets/bg-texture.png'); */

}

/*=================General Styles============*/

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

h2{
    font-weight: 900;
}
/* #header{
    height: 100vh;
}
#contact{
    height: 80vh;
} */
section {
    margin-top: 4rem;
}

section>h2,
section>h5 {
    text-align: center;
    color: var(--color-light);
}

section>h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

