.footer {
    width: 100%;
    height: 50vh;
    background: linear-gradient(to right bottom, #28b485, #7ed56f);
    padding: 30px 40px;
    border-top-left-radius: 45px;
    /* position: absolute;
    bottom: 0; */
    position: sticky;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: start;
}

.footer .brand {
    width: 200px;
    height: auto;
}

.footer .mail {
    display: grid;
    grid-template-rows: repeat(3, 0.3fr);
    row-gap: 10px;
    /* justify-content: center; */
}

.mail h4 {
    /* justify-self: center; */
    font-size: 1.3rem;
    font-weight: 900;
}

.footer .mail input {
    width: 80%;
    height: 40px;
    border-radius: 10px;
    padding: 5px 8px;
    border-color: #fff;
    /* text-align: center; */
}

.footer .mail .msg {
    height: 120px;
}

.mapp {
    width: 90%;
    height: 300px;
}

input {
    background: #ecf0f3;
    padding: 10px;
    padding-left: 20px;
    height: 50px;
    font-size: 14px;
    border-radius: 50px;
    border: 1px solid black;
    box-shadow: inset 6px 6px 6px #cbced1, inset -6px -6px 6px white;
}