#team{
    height: 100vh;
    width: 100%;
    margin: 80px auto;
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    justify-content: center;
    row-gap: 30px;
    margin-bottom: 16% ;
    /* background : linear-gradient(to right bottom, #7ed56f, #28b485); */
}

.section-heading{
    color: #28b485;
    text-align: center;
}

.row{
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20%;
}

.mentor{
    grid-template-columns: 1fr;
    justify-self: center;
}

.card{
    position: relative;
    width: 350px;
    height: 240px;
    background-color: #fff;
    transition: 0.5s;
    display: grid;
    grid-template-rows: 0fr 3fr 1fr;
}

.card:hover{
    width: 450px;
}

.card:hover .row{
    grid-template-columns: 0;
}
.card .lines{
    position: absolute;
    inset: 0;
    background-color: #fddc5c;
    overflow: hidden;
}

.card .lines::before{
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 600px;
    height: 120px;
    background: linear-gradient(transparent, #28b485
    , #28b485
    , #28b485, transparent 
    );
    animation: animate 4s linear infinite;
}

@keyframes animate {
    0%{
        transform: translate(-50%, -50%) rotate(0deg);
    }

    100%{
        transform: translate(-50%, -50%) rotate(360deg);
    }
}

.card .lines::after{
    content: "";
    position: absolute;
    inset: 4px;
    background-color: #fff;
}

.card .imgbox{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100px;
    height: 100px;
    transform: translate(-50%, -90%);
    background-color: aqua;
    transition: 0.5s;
    z-index: 10;
    overflow: hidden;
    justify-content: center;
    border-radius: 50%;
}

.card:hover .imgbox{
    /* top: 20px; */
    transform: translate(-150%, -50%);
}

