} */

.card .imgbox img{
    z-index: 999;
}

.profile-text{
    display: grid;
    grid-template-rows: 1fr 1fr;
    row-gap: 8px;
    position: absolute;
    width: 100%;
    top: 80%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 999;
    color: #000;
    text-align: center;
}

.name{
    font-size: 1.2rem;
}

.designation{
    font-size: 0.7rem;
}

.card:hover .profile-text{
    transform: translate(-40%, -130%);
    z-index: 999;
}

