



#programs{
    /* height: 100vh; */
    padding: 40px;
    display: grid;
    grid-template-rows: 0.2fr 1fr 1fr 1fr;
    row-gap: 0.7%;
}

h3.sc{
    color: #28b485;
}

.fruits-spoilage {
    /* height: 80vh; */
    height: 50%;
    display: grid;
    grid-template-columns: 2.5fr 1fr;
    column-gap: 5%;
    align-items: center;
    justify-content: center;
}

.fruits-text{
    display: grid;
    color: Black;
    grid-template-rows: 1fr 3fr;
    row-gap: 20%;
}

.fruits-text h3{
    align-self: flex-start;
    text-align: center;
}
.fruits-text p{
    text-align: center;
    width: 90%;
    justify-self: center;
}

.fruits-img{
    width: 90%;
    height: 100%;
}

.fruits-img img{
    justify-self:end;
    width: 90%;
    height: 100%;
}

.workout {
    height: 50%;
    display: grid;
    place-items: center;
    grid-template-columns: 1fr 2.5fr;
    column-gap: 2%;
    /* align-items: center;
    justify-items: center;
    justify-content: center; */
}

.workout-text{
    display: grid;
    color: Black;
    grid-template-rows: 1fr 3fr;
    row-gap: 20%;
}

.workout-text h3{
    text-align: center;
}

.workout-text p{
    align-self: flex-start;
    text-align: center;
    width: 90%;
    justify-self: center;
}

.workout-img{
    width: 90%;
    height: 100%;
    justify-self: center;
}

.workout-img img{
    justify-self:center;
    width: 90%;
    height: 100%;
    place-self: center;
}

.diet {
    height: 50%;
    display: grid;
    grid-template-columns: 2.5fr 1fr;
    column-gap: 5%;
    align-items: center;
    justify-content: center;
}

.diet-text{
    display: grid;
    color: Black;
    grid-template-rows: 1fr 3fr;
    row-gap: 20%;
}

.diet-text h3{
    align-self: flex-start;
    text-align: center;
}
.diet-text p{
    align-self: flex-start;
    text-align: center;
    width: 90%;
    justify-self: center;
}

.diet-img{
    width: 90%;
    height: 100%;
}

.diet-img img{
    justify-self:end;
    width: 90%;
    height: 100%;
}

lottie-player{
    width: 500px;
    height: 400px;
}

.feature-heading{
    font-family: 'Lato', sans-serif;
    font-weight: 400;
    color: #28b485;
    font-size: 1.4rem;
    letter-spacing: 0.17rem;
}


.feature-text{
    color: #A9A9A9;
    font-size: 0.9rem;
    text-align: justify;
}
